body {
  font-family: Arial, sans-serif;
  background-color: #f8f8f8;
  margin: 0;
  padding: 0;
}

.container {
  width: 80%;
  margin: auto;
  background-color: #fff;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  margin-top: 20px;
}

header {
  text-align: center;
  margin-bottom: 20px;
}

.dog-list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.filters {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 20px;
}

.filters label {
  display: block;
}

.filters input,
.filters select {
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 200px;
}

.dog-details {
  text-align: left;
}

.dog-details img {
  width: 300px;
  height: auto;
  border-radius: 8px;
  margin-bottom: 20px;
  text-align: center;

}
/* General styling for DogCard */
.dog-card {
  border: 1px solid #ddd;
  padding: 16px;
  margin: 16px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s;
  position: relative;
}

.dog-card img {
  max-width: 100%;
  border-radius: 8px;
}

.dog-card h3 {
  margin-top: 0;
  font-size: 1.5em;
}

.dog-card p {
  margin: 8px 0;
}

.dog-card a {
  position: absolute;
  bottom: 16px;
  right: 16px;
  color: #007bff;
  text-decoration: none;
}
